<template>
  <v-container class="article">
    <v-alert 
      :value="alert.visible"
      :dismissible="true"
      :color="alert.color"
      :type="alert.type">
      {{alert.message}}
    </v-alert>
    <h1>{{title}}</h1>
    <p>{{description}}</p>
    <v-row 
      class="mb-3"
    >
      <v-col cols="12"
        :lg="6"
        :md="12"
      >
        <h2>Project Description</h2>
        <iframe width="100%" height="315" :src="video" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
        <span v-html="explanation"></span>
      </v-col>
      <v-col cols="12"
        :lg="6"
        :md="12"
      >
        <h2>Project Design</h2>
         <v-row>
          <v-col
            v-for="image in gallery"
            :key="image"
            class="d-flex child-flex"
            cols="12"
            :md="3"
            :sm="6"
          >
            <v-img
              v-if="images"
              lazy-src="@/assets/landing-background-vignette.png"
              :src="image"
              aspect-ratio="1"
              class="grey lighten-2"
            >
              <template v-slot:placeholder>
                <v-row
                  class="fill-height ma-0"
                  align="center"
                  justify="center"
                >
                  <v-progress-circular
                    indeterminate
                    color="grey lighten-5"
                  ></v-progress-circular>
                </v-row>
              </template>
            </v-img>
          </v-col>
        </v-row>
        <br />
        <span v-html="elaboration"></span>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import projects from '@/services/projects'

export default {
  data: () => ({
    author: '..', 
    date: '..',
    title: 'Loading...',
    description: '',
    video: '',
    explanation: '',
    elaboration: '',
    gallery: [],
    images: false,
    alert: {
      visible: false,
      color: 'red',
      type: 'error',
      message: ''
    }
  }),
  async mounted () {
    const article = projects.getArticle(this.$route.params.id)
    this.author = article.author
    this.title = article.title
    this.description = article.description
    this.date = projects.getDate(article.created_at)
    this.explanation = article.explanation
    this.elaboration = article.elaboration
    this.video = article.video
    this.gallery = article.gallery.split(',')
    this.images = this.gallery[0] !== ''
  }
}
</script>

<style scoped>
  a {
    color: #f9c25b;
    text-decoration: none;
  }
  .article {
    font-family: "Montserrat";
    color: #cccccc;
    font-weight: 400;
    font-size: 14px;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    line-height: 1.8;
    margin-left: auto;
    margin-right: auto;
  }
</style>
